main {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(1, 200px);
  gap: 46px 48px;
  padding: 50px 40px;
}

@media (min-width: 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (min-width: 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (min-width: 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  display: flex;
  flex-direction: column;
  width: 166px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;

  &__product-picture {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;

    &_photo {
      width: 160px;
      height: 134px;
    }
  }

  &__product-name {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 4px;
    color: $price-review-color;
  }

  &__product-code {
    font-size: 10px;
    line-height: 14px;
    color: $text-code-color;
    margin-bottom: 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    line-height: 14px;
    text-align: right;
    color: $price-review-color;
    margin-top: 2px;
  }

  &__product-price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__product-price-text {
    line-height: 18px;
    text-align: left;
    color: $text-code-color;
  }

  &__product-price-price {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    text-align: right;
    color: $price-review-color;
  }

  &__button {
    height: 40px;
    width: 100%;
    font-size: 14px;
    font-weight: 700;
    font-family: Roboto, sans-serif;
    color: $main-color;
    background-color: $bottom-color;
    border: 1px solid $main-color;
    border-radius: 5px;
  }

  &__button:hover {
    background-color: $main-color;
    color: $bottom-color;
    border-color: $bottom-color;
  }
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: #34568b;
}
