html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  animation-duration: .3s;
}

body, h1, h2, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.img {
  width: 40px;
  height: 40px;
}

.nav__list {
  justify-content: space-between;
  align-items: stretch;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  flex-direction: column;
  align-content: flex-end;
  height: 60px;
  margin-left: 20px;
  font-weight: 500;
  line-height: 60px;
  list-style-type: none;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:hover {
  color: #060b35;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  text-decoration: none;
}

a[href="#apple"]:link {
  color: #00acdc;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: #00acdc;
}

main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 166px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__product-picture {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.card__product-picture_photo {
  width: 160px;
  height: 134px;
}

.card__product-name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  margin-top: 2px;
  font-size: 10px;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__product-price-text {
  text-align: left;
  color: #616070;
  line-height: 18px;
}

.card__product-price-price {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
}
